"use client";
import React, { useRef, useEffect, useState } from "react";

const GAME_RATIO = 16 / 9;

const GAME_WIDTH = 880;
const GAME_HEIGHT = 495; // 16:9 ratio
const PADDLE_WIDTH = 12;
const PADDLE_HEIGHT = 90;
const BALL_SIZE = 16;
const PADDLE_SPEED = 7;
const BALL_SPEED = 6;

type PingPongGameProps = {
  player1: {
    name: string;
    avatar: string;
    username: string;
  };
  player2: {
    name: string;
    avatar: string;
    username: string;
  };
  onExit: () => void;
};

const isMobile = () => typeof window !== 'undefined' && window.innerWidth < 640;

const PingPongGame: React.FC<PingPongGameProps> = ({ player1, player2, onExit }) => {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const [scores, setScores] = useState({ p1: 0, p2: 0 });
  const [running, setRunning] = useState(false);
  const [paused, setPaused] = useState(false);
  const [gameStarted, setGameStarted] = useState(false);
  const [mobile, setMobile] = useState(isMobile());
  const [canvasDims, setCanvasDims] = useState<{ width: number; height: number }>({
    width: GAME_WIDTH,
    height: GAME_HEIGHT,
  });

  // Paddle positions: player1 left, player2 right
  const paddle1Y = useRef<number>(GAME_HEIGHT / 2 - PADDLE_HEIGHT / 2);
  const paddle2Y = useRef<number>(GAME_HEIGHT / 2 - PADDLE_HEIGHT / 2);
  const ball = useRef({
    x: GAME_WIDTH / 2 - BALL_SIZE / 2,
    y: GAME_HEIGHT / 2 - BALL_SIZE / 2,
    dx: BALL_SPEED * (Math.random() > 0.5 ? 1 : -1),
    dy: BALL_SPEED * (Math.random() > 0.5 ? 1 : -1),
  });
  const keys = useRef<{ [key: string]: boolean }>({});

  // Mobile paddle state
  const [paddle1Move, setPaddle1Move] = useState<'' | 'up' | 'down'>('');
  const [paddle2Move, setPaddle2Move] = useState<'' | 'up' | 'down'>('');

  // Responsive canvas
  useEffect(() => {
    const handleResize = () => {
      setMobile(isMobile());
      // Maintain area with max 90vw, max 60vh, keep 16:9
      const maxW = Math.min(window.innerWidth * 0.97, 1100);
      const maxH = Math.min(window.innerHeight * 0.6, 640);
      let width = maxW, height = width / GAME_RATIO;
      if (height > maxH) {
        height = maxH; width = height * GAME_RATIO;
      }
      setCanvasDims({ width, height });
    };
    handleResize();
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  // Keyboard controls (Desktop)
  useEffect(() => {
    if (mobile) return;
    const downHandler = (e: KeyboardEvent) => {
      keys.current[e.key.toLowerCase()] = true;
    };
    const upHandler = (e: KeyboardEvent) => {
      keys.current[e.key.toLowerCase()] = false;
    };
    window.addEventListener("keydown", downHandler);
    window.addEventListener("keyup", upHandler);
    return () => {
      window.removeEventListener("keydown", downHandler);
      window.removeEventListener("keyup", upHandler);
    };
  }, [mobile]);

  // Game loop
  useEffect(() => {
    if (!gameStarted || paused) return;
    let animation: number;

    const draw = () => {
      const ctx = canvasRef.current?.getContext("2d");
      if (!ctx) return;
      // Clear bg with dark gradient look
      const grad = ctx.createLinearGradient(0, 0, canvasDims.width, canvasDims.height);
      grad.addColorStop(0, "#23272f");
      grad.addColorStop(1, "#15181e");
      ctx.fillStyle = grad;
      ctx.fillRect(0, 0, canvasDims.width, canvasDims.height);

      // Mid lines (cross style)
      ctx.strokeStyle = "rgba(255,255,255,0.08)";
      ctx.lineWidth = 2;
      // Vertical
      ctx.beginPath();
      ctx.moveTo(canvasDims.width / 2, 0);
      ctx.lineTo(canvasDims.width / 2, canvasDims.height);
      ctx.stroke();
      // Horizontal
      ctx.beginPath();
      ctx.moveTo(0, canvasDims.height / 2);
      ctx.lineTo(canvasDims.width, canvasDims.height / 2);
      ctx.stroke();

      // Paddles
      ctx.fillStyle = "#fafbff";
      ctx.shadowColor = "#20242a";
      ctx.shadowBlur = 7;
      ctx.fillRect(0, paddle1Y.current * canvasDims.height / GAME_HEIGHT, PADDLE_WIDTH * canvasDims.width / GAME_WIDTH, PADDLE_HEIGHT * canvasDims.height / GAME_HEIGHT);
      ctx.fillRect((GAME_WIDTH - PADDLE_WIDTH) * canvasDims.width / GAME_WIDTH, paddle2Y.current * canvasDims.height / GAME_HEIGHT, PADDLE_WIDTH * canvasDims.width / GAME_WIDTH, PADDLE_HEIGHT * canvasDims.height / GAME_HEIGHT);
      ctx.shadowBlur = 0;

      // Ball
      ctx.beginPath();
      ctx.arc(
        ball.current.x * canvasDims.width / GAME_WIDTH + BALL_SIZE * canvasDims.width / GAME_WIDTH / 2,
        ball.current.y * canvasDims.height / GAME_HEIGHT + BALL_SIZE * canvasDims.height / GAME_HEIGHT / 2,
        BALL_SIZE * canvasDims.width / GAME_WIDTH / 2,
        0, Math.PI * 2
      );
      ctx.fillStyle = "#f7f7fa";
      ctx.shadowColor = "#fff";
      ctx.shadowBlur = 6;
      ctx.fill();
      ctx.shadowBlur = 0;
    };

    const update = () => {
      // Move paddles - desktop or mobile
      if (!mobile) {
        if (keys.current["w"] && paddle1Y.current > 0) paddle1Y.current -= PADDLE_SPEED;
        if (keys.current["s"] && paddle1Y.current < GAME_HEIGHT - PADDLE_HEIGHT) paddle1Y.current += PADDLE_SPEED;
        if ((keys.current["arrowup"] || keys.current["‚Üë"]) && paddle2Y.current > 0) paddle2Y.current -= PADDLE_SPEED;
        if ((keys.current["arrowdown"] || keys.current["‚Üì"]) && paddle2Y.current < GAME_HEIGHT - PADDLE_HEIGHT) paddle2Y.current += PADDLE_SPEED;
      } else {
        // Touch/mobile, button-based control
        if (paddle1Move === "up" && paddle1Y.current > 0) paddle1Y.current -= PADDLE_SPEED;
        if (paddle1Move === "down" && paddle1Y.current < GAME_HEIGHT - PADDLE_HEIGHT) paddle1Y.current += PADDLE_SPEED;
        if (paddle2Move === "up" && paddle2Y.current > 0) paddle2Y.current -= PADDLE_SPEED;
        if (paddle2Move === "down" && paddle2Y.current < GAME_HEIGHT - PADDLE_HEIGHT) paddle2Y.current += PADDLE_SPEED;
      }

      // Ball movement
      ball.current.x += ball.current.dx;
      ball.current.y += ball.current.dy;

      // Wall collision
      if (ball.current.y <= 0 || ball.current.y + BALL_SIZE >= GAME_HEIGHT) {
        ball.current.dy *= -1;
      }
      // Left paddle collision
      if (
        ball.current.x <= PADDLE_WIDTH &&
        ball.current.y + BALL_SIZE >= paddle1Y.current &&
        ball.current.y <= paddle1Y.current + PADDLE_HEIGHT
      ) {
        ball.current.dx = Math.abs(ball.current.dx);
      }
      // Right paddle collision
      if (
        ball.current.x + BALL_SIZE >= GAME_WIDTH - PADDLE_WIDTH &&
        ball.current.y + BALL_SIZE >= paddle2Y.current &&
        ball.current.y <= paddle2Y.current + PADDLE_HEIGHT
      ) {
        ball.current.dx = -Math.abs(ball.current.dx);
      }
      // Scoring
      if (ball.current.x < -BALL_SIZE) {
        setScores((s) => ({ ...s, p2: s.p2 + 1 }));
        resetBall(-1);
      } else if (ball.current.x > GAME_WIDTH + BALL_SIZE) {
        setScores((s) => ({ ...s, p1: s.p1 + 1 }));
        resetBall(1);
      }
    };

    const resetBall = (direction: number) => {
      ball.current.x = GAME_WIDTH / 2 - BALL_SIZE / 2;
      ball.current.y = GAME_HEIGHT / 2 - BALL_SIZE / 2;
      // Give the ball a random Y direction each serve
      const yDirection = (Math.random() > 0.5 ? 1 : -1) * BALL_SPEED;
      ball.current.dx = BALL_SPEED * direction;
      ball.current.dy = yDirection;
    };

    const loop = () => {
      if (!paused) update();
      draw();
      animation = requestAnimationFrame(loop);
    };

    loop();
    return () => {
      cancelAnimationFrame(animation);
    };
    // eslint-disable-next-line
  }, [gameStarted, paused, mobile, canvasDims]);

  // Win condition: First to 5
  useEffect(() => {
    if (scores.p1 >= 5 || scores.p2 >= 5) {
      setPaused(true);
    }
  }, [scores]);

  // Touch button event helpers
  const handleMobilePress = (which: 'p1up' | 'p1down' | 'p2up' | 'p2down', isDown: boolean) => {
    if (which === 'p1up') setPaddle1Move(isDown ? 'up' : '');
    if (which === 'p1down') setPaddle1Move(isDown ? 'down' : '');
    if (which === 'p2up') setPaddle2Move(isDown ? 'up' : '');
    if (which === 'p2down') setPaddle2Move(isDown ? 'down' : '');
  };

  // Reset/exit helpers
  const handleStart = () => {
    setScores({ p1: 0, p2: 0 });
    paddle1Y.current = GAME_HEIGHT / 2 - PADDLE_HEIGHT / 2;
    paddle2Y.current = GAME_HEIGHT / 2 - PADDLE_HEIGHT / 2;
    ball.current.x = GAME_WIDTH / 2 - BALL_SIZE / 2;
    ball.current.y = GAME_HEIGHT / 2 - BALL_SIZE / 2;
    ball.current.dx = BALL_SPEED * (Math.random() > 0.5 ? 1 : -1);
    ball.current.dy = BALL_SPEED * (Math.random() > 0.5 ? 1 : -1);
    setGameStarted(true);
    setPaused(false);
  };

  // UI helpers
  const gameOver = scores.p1 >= 5 || scores.p2 >= 5;

  return (
    <div className="fixed inset-0 w-full h-full flex flex-col items-center justify-center bg-[#15181e] z-[60] overflow-auto">
      <div className="w-full flex flex-col items-center pt-6 pb-10 justify-center px-2 md:px-0">
        {/* Game Card */}
        <div
          className="flex flex-col items-center w-full"
          style={{ maxWidth: 1050, width: "100%" }}>
          <div className="w-full flex flex-col items-center rounded-3xl shadow-2xl border border-[#50545d] bg-gradient-to-b from-[#23272f] to-[#191b20] py-4">
            <div className="w-full flex flex-col items-center px-2 md:px-8">
              {/* Game area */}
              <div className="w-full flex justify-center mb-5 mt-2">
                <div
                  className="max-w-full max-h-[60vh] rounded-2xl border border-[#656872] bg-[#222429] shadow-2xl flex items-center justify-center overflow-hidden"
                  style={{
                    aspectRatio: "16/9",
                    width: canvasDims.width,
                    height: canvasDims.height,
                    minWidth: 220, minHeight: 120
                  }}>
                  <canvas
                    ref={canvasRef}
                    width={GAME_WIDTH}
                    height={GAME_HEIGHT}
                    className="block w-full h-full"
                    style={{
                      background: "transparent"
                    }}
                  />
                  {/* Center Play Button */}
                  {!gameStarted && (
                    <button
                      onClick={handleStart}
                      className="absolute z-20 flex items-center justify-center w-20 h-20 rounded-full bg-black/55 border-4 border-white/80 hover:bg-black/80 hover:scale-110 duration-150 transition-all"
                      style={{
                        left: "50%",
                        top: "50%",
                        transform: "translate(-50%,-50%)",
                      }}
                    >
                      <svg width={40} height={40} viewBox="0 0 24 24" fill="#fff">
                        <polygon points="8,6 19,12 8,18" />
                      </svg>
                    </button>
                  )}
                  {/* Win Overlay */}
                  {gameOver && (
                    <div className="absolute inset-0 z-30 bg-black/65 flex flex-col items-center justify-center">
                      <div className="text-4xl md:text-6xl font-bold text-white mb-6 drop-shadow-xl">
                        {scores.p1 > scores.p2
                          ? `${player1.name} Wins! üèÜ`
                          : `${player2.name} Wins! üèÜ`}
                      </div>
                      <button
                        onClick={() => {
                          setGameStarted(false);
                          setScores({ p1: 0, p2: 0 });
                        }}
                        className="px-8 py-4 bg-blue-600 hover:bg-blue-700 transition-colors text-white font-semibold rounded-xl text-xl"
                      >Play Again</button>
                    </div>
                  )}
                  {/* Mobile: Touch controls for both players */}
                  {mobile && gameStarted && !gameOver && (
                    <>
                      <div className="absolute left-0 top-0 h-full flex flex-col justify-center gap-3 p-1 z-20">
                        <button
                          className="mobile-ctrl-btn"
                          style={{
                            width: 48,
                            height: 48,
                            background: "#2d3748cc",
                            borderRadius: 12,
                            color: 'white',
                            fontWeight: 900,
                            fontSize: 22,
                            marginBottom: 8,
                          }}
                          onTouchStart={() => handleMobilePress('p1up', true)}
                          onTouchEnd={() => handleMobilePress('p1up', false)}
                        >‚Üë</button>
                        <button
                          className="mobile-ctrl-btn"
                          style={{
                            width: 48,
                            height: 48,
                            background: "#2d3748cc",
                            borderRadius: 12,
                            color: 'white',
                            fontWeight: 900,
                            fontSize: 22
                          }}
                          onTouchStart={() => handleMobilePress('p1down', true)}
                          onTouchEnd={() => handleMobilePress('p1down', false)}
                        >‚Üì</button>
                      </div>
                      <div className="absolute right-0 top-0 h-full flex flex-col justify-center gap-3 p-1 z-20">
                        <button
                          className="mobile-ctrl-btn"
                          style={{
                            width: 48,
                            height: 48,
                            background: "#2d3748cc",
                            borderRadius: 12,
                            color: 'white',
                            fontWeight: 900,
                            fontSize: 22,
                            marginBottom: 8,
                          }}
                          onTouchStart={() => handleMobilePress('p2up', true)}
                          onTouchEnd={() => handleMobilePress('p2up', false)}
                        >‚Üë</button>
                        <button
                          className="mobile-ctrl-btn"
                          style={{
                            width: 48,
                            height: 48,
                            background: "#2d3748cc",
                            borderRadius: 12,
                            color: 'white',
                            fontWeight: 900,
                            fontSize: 22
                          }}
                          onTouchStart={() => handleMobilePress('p2down', true)}
                          onTouchEnd={() => handleMobilePress('p2down', false)}
                        >‚Üì</button>
                      </div>
                    </>
                  )}
                </div>
              </div>
              {/* Scores and Players (Responsive) */}
              <div className="flex items-center justify-between w-full px-2 sm:px-8 my-3">
                {/* Player 1 area */}
                <div className="flex items-center gap-3 min-w-0">
                  <img
                    src={player1.avatar}
                    alt={player1.name}
                    className="w-12 h-12 rounded-full border-2 border-[#BFD6ED] object-cover"
                  />
                  <div className="flex flex-col min-w-0">
                    <span className="font-semibold text-base text-white truncate">{player1.name}</span>
                    <span className="text-xs text-gray-300">@{player1.username}</span>
                  </div>
                </div>
                {/* Score block */}
                <div className="flex flex-col items-center px-3">
                  <div className="flex text-3xl md:text-5xl font-bold mb-1 gap-2 text-white">
                    <span>{scores.p1}</span>
                    <span>-</span>
                    <span>{scores.p2}</span>
                  </div>
                  <span className="uppercase text-xs font-semibold tracking-wide text-gray-400">Score</span>
                </div>
                {/* Player 2 area */}
                <div className="flex items-center gap-3 min-w-0 flex-row-reverse">
                  <img
                    src={player2.avatar}
                    alt={player2.name}
                    className="w-12 h-12 rounded-full border-2 border-[#BFD6ED] object-cover"
                  />
                  <div className="flex flex-col min-w-0 text-right">
                    <span className="font-semibold text-base text-white truncate">{player2.name}</span>
                    <span className="text-xs text-gray-300">@{player2.username}</span>
                  </div>
                </div>
              </div>
              {/* Game Controls */}
              <div className="flex flex-col md:flex-row gap-2 items-center justify-center w-full mt-4 mb-2">
                {gameStarted && !paused && !gameOver && (
                  <button
                    onClick={() => setPaused(true)}
                    className="bg-blue-600 hover:bg-blue-700 text-white px-6 py-3 min-w-[120px] rounded-lg text-lg font-semibold transition-colors shadow"
                  >
                    Pause
                  </button>
                )}
                {gameStarted && paused && !gameOver && (
                  <button
                    onClick={() => setPaused(false)}
                    className="bg-green-600 hover:bg-green-700 text-white px-6 py-3 min-w-[120px] rounded-lg text-lg font-semibold transition-colors shadow"
                  >
                    Resume
                  </button>
                )}
                <button
                  onClick={onExit}
                  className="bg-[#55595f] hover:bg-[#35373a] text-white px-6 py-3 min-w-[120px] rounded-lg text-lg font-semibold transition-colors shadow"
                >
                  Exit
                </button>
              </div>
              {/* Instructions (desktop) */}
              {!mobile && (
                <div className="flex flex-row gap-8 justify-between items-center w-full mt-4">
                  <div className="font-mono text-sm text-blue-100 whitespace-nowrap">
                    Player 1 (Left): <span className="font-bold">W/S ‚Äî Move Up/Down</span>
                  </div>
                  <div className="font-mono text-sm text-blue-100 whitespace-nowrap">
                    Player 2 (Right): <span className="font-bold">‚Üë/‚Üì ‚Äî Move Up/Down</span>
                  </div>
                </div>
              )}
              {/* Instructions (mobile) */}
              {mobile && (
                <div className="mt-2 text-center text-xs text-blue-200">
                  Use the left/right side controls to move paddles!
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PingPongGame;
